* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

body {
  background: black;
  color: white;
  
}


.wrapper__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}


.card {
  border: 1px solid black;
  width: 27rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 2rem;
  margin-left: 2rem;
  /* background: black; */
  border-bottom: 1px solid white;
}

.icon svg {
  font-size: 3rem;
  color: rgb(240, 108, 180);
}

.boton__sensurado {
  margin: 0;
  padding: 0;
}

.boton__sensurado img{
  width: 100%;
  overflow: hidden;
  border-radius: 10px;
}

.image__container  {
  width: 25rem;
  margin-top: 1rem;
  background: transparent;
}

.image__container img {
  width: 100%;
  user-select: none;
}


/* ============  CARD =================== */

.titulo {
  margin: .5rem 1rem;
  text-align: center;
  width: 90%;
  font-size: 1.5rem;
  font-weight: bold;
  background: black;
  color: rgb(252, 240, 220);
}

.descripcion {
  font-size: 1rem;
  margin: .5rem 0;
  font-style: italic;
}

.boton__left {
  border-bottom: 1px solid white;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  margin-top: 1rem;
  font-style: oblique;
  
}

.boton__right {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  margin-top: 1rem;
}

button {
  width: fit-content;
  padding: 1rem;
  border-radius: 15px;
  cursor: pointer;
  user-select: none;
}

button a {
  color: black;
  font-weight: 600;
  outline: none;
}


.azul {
  background: rgb(160, 160, 248);
  outline: none;
  color: white;
}

.rojo {
  background: rgb(250, 109, 109);
}

.descripcion__boton {
  padding: 1rem;
  font-style: italic;
}

/* ======================== HOME ======================= */

.home {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.home__container {
  width: 30rem;
  height: 50rem;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.home__container h1 {
  position: relative;
  z-index: 2;
  margin-bottom: 4rem;
  padding: 1rem;
  line-height: 3rem;
  font-weight: bold;
  width: 100%;
  text-align: center;
}

.home__container button {
  position: relative;
  z-index: 4;
  outline: none;
  border: 0;
  padding: 1rem 3rem;
  margin-top: 3rem;
}

.home__container button a {
  font-size: 1.5rem;
}

.home__container img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: auto;
  opacity: 0.6;

}